*
{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: cursive;
}

.navbar
{
    border: 2px solid black;
    height: 100px;
    width: 100%;
    align-content: center;
    text-align: center;
    background-color: rgb(165, 136, 42);
}

.add-box
{
    border: 2px solid orange;
    display: inline-block;
    margin-right: 100px;
    font-size: xx-large;
    background-color: rgb(226, 168, 43);
    color: black;
    border-radius: 10px ;
    text-decoration: none;
    padding: 20px;
    transition: all;

}
.add-box:hover {
    transform: scale(1.1);
  }
  
.list-box
{
    border: 2px solid orange;
    display: inline-block;
    font-size: xx-large;
    background-color: rgb(226, 168, 43);
    color: black;
    border-radius: 10px ;
    text-decoration: none;
    padding: 20px;
    margin-left: 100px;
    transition: all;
}
.list-box:hover {
    transform: scale(1.1);
  }


