*
{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: cursive;
}

.details
{
    /* border: 2px solid black; */
    text-align: center;
    background-color: black;
    color: whitesmoke;
}

.details h1{
    font-size: xx-large;
}


.singledata li
{
    /* border: 2px solid rgb(152, 16, 16); */
    height: 200px;
    display: flex;
    justify-content: center;
    gap: 100px;
}
.div1
{
    /* border: 2px solid rgb(23, 152, 16); */
    text-align: left;
    height: 100%;
    width: 300px;
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.div2
{
    /* border: 2px solid rgb(136, 16, 152); */
    text-align: left;
    height: 100%;
    width: 300px;
    display: flex;
    flex-direction: column;
    gap: 50px;
    justify-content: center;
}
.div2 p
{
    font-size:30px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

