*
{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: cursive;
}
.courier
{
    background-color: grey;
}

.form-data h1
{
    font-size: xx-large;
}

.form-data
{
    /* border: 2px solid red; */
    display: flex;
    flex-direction: column;
    align-items: center;

}
.container
{
    border: 2px solid rgb(0, 128, 255); 
    height : 40px;
}
.div
{
    border: 2px solid rgb(0, 255, 204); 
    height: 100%;
}

.div input
{
    border: 2px solid sandybrown;
    display: inline-block ;
    height: 100%
}
.div input::placeholder
{
    text-align: center;
}

.div select
{
    height: 100%;
    text-align: center;
}

.press
{   
    border: 2px solid green;
    height: 70px;
    width: 150px;
    margin-top: 30px;
}

button
{
    background-color: rgb(73, 72, 74);
    color: rgb(106, 196, 10);
    height: 100%;
    width: 100%;
}

small
{
    color: red;
}

.output
{
    border: 2px solid orange;
    margin-top: 20px;
    font-size: 50px;
    background-color: orange;
}